  created() {},
  mounted() {
    if (window.requestIdleCallback) {
      requestIdleCallback(() => {
        Fingerprint2.get((components) => {
          this.fpc = components;
          this.mapping(this.fpc);
          const values = this.filterFpc.map((component) => {
            return component.value;
          });
          this.fpid = Fingerprint2.x64hash128(values.join(""), 31);
        });
      });
    } else {
      setTimeout(() => {
        Fingerprint2.get((components) => {
          this.fpc = components;
          const values = this.filterFpc.map((component) => {
            return component.value;
          });
          this.fpid = Fingerprint2.x64hash128(values.join(""), 31);
        });
      }, 500);
    }
  },
  methods: {
    mapping(component) {
      const target = this.safari;
      const result = [];
      component.forEach((item) => {
        const tItem = target.find((ti) => {
          return ti.key === item.key;
        });
        if (tItem && tItem.value === item.value) {
          result.push(item);
        }
      });
      this.result = result;
    },
  },
  computed: {
    filterFpc() {
      const allowKeys = [
        "webdriver",
        "language",
        "hardwareConcurrency",
        "timezoneOffset",
        "timezone",
        "sessionStorage",
        "localStorage",
        "indexedDb",
        "addBehavior",
        "cpuClass",
        "platform",
        "hasLiedLanguages",
        "hasLiedResolution",
        "hasLiedOs",
        "hasLiedBrowser",
      ];
      return this.fpc.filter((item) => {
        return allowKeys.some((k) => k === item.key);
      });
    },
  },
};
